export default {
    root: {
      backgroundColor: "white",
      border: "1px solid black",
      borderRadius: "5px",
      padding: "0.5rem",
      position: "relative",
      overflow: "hidden",
      cursor: "pointer",
      "&:hover svg": {
        opacity: 1
      }
    },
    colors: {
      backgroundColor: "#dae1e4",
      height: "150px",
      width: "100%",
      borderRadius: "5px",
      overflow: "hidden"
    },
    title: {
      display: "flex",
      justifyContent: "space-between",
      alignItems: "center",
      margin: "0",
      color: "black",
      paddingTop: "0.5rem",
      position: "relative"
    },
    emoji: {
      marginLeft: "0.5rem",
      fontSize: "1rem"
    },
    miniColor: {
      height: "25%",
      width: "20%",
      display: "inline-block",
      margin: "0 auto",
      position: "relative",
      marginBottom: "-3.5px"
    },
    deleteIcon:{

      "&:hover":{
        borderRadius: "50%",
        color:"white",
        backgroundColor:"red",
        transform:"scale(1.2,1.2)"
      }
    }
  };